.header {
	height: 80px;
	display: flex;
	padding: 0 22px;
	align-items: center;
	background-color: #fff;
	justify-content: space-between;
 }
 
 .title {
	font-size: 18px;
	font-weight: 400;
	line-height: 28px;
	cursor: pointer;

	&:hover {
	  color: #1890ff;
	}
 }
 
 .btns-wrapper {
	display: flex;
	flex-basis: 212px;
	align-items: center;
	justify-content: space-between;
 }
 
 .signIn,
 .signUp {
	font-size: 18px;
	line-height: 28px;
	background-color: #fff;
	padding: 6px 18px 10px;
	color: rgba(0, 0, 0, 0.85);
	border: 1px solid transparent;
   cursor: pointer;

	&:hover {
	  color: #52c41a;
	}
 }

 .active {
	color: #52c41a;
	font-size: 18px;
	line-height: 28px;
	border-radius: 5px;
	padding: 6px 18px 10px;
	background-color: #fff;
	border: 1px solid #52c41a;
 
	&:hover {
	  color: #52c41a;
	}
 }
